<template lang="">
    <div>
        <div id="modal__gallery" class="modal" v-if="isShowingModal">
            <div class="modal__content">
                <span class="modal__close" @click="closeModal">&times;</span>
                <div class="modal__body" tabindex="0" @focusout="closeModal">
                    <img :src="modalImg.img" class="show__gallery" />
                </div>
            </div>
        </div>
        <main class="main">
            <div class="container">
                <ul class="breadcrumb">
                    <li class="breadcrumb__item breadcrumb__item--hide">
                        <a href="" class="breadcrumb__link"><span class="breadcrumb__span">صفحه اصلی</span></a>
                    </li>
                    <li class="breadcrumb__item">
                        <a href="" class="breadcrumb__link"><span class="breadcrumb__span">فهرست محصولات</span></a>
                    </li>
                    <li class="breadcrumb__item">
                        <a href="" class="breadcrumb__link"><span class="breadcrumb__span">محصول شماره یک</span></a>
                    </li>
                </ul>
                <div class="product">
                    <div class="product__header">
                        <div class="product__expiration">
                            <div class="count-down__timer" id="count-dowm__timer">
                                {{ dif }}
                            </div>
                        </div>
                        <div class="product__row">
                            <div class="prodcut__gallery">
                                <div class="gallery">
                                    <div class="gallery__slideshow">
                                        <span class="gallery__count">
                                            <span class="gallery__number1">{{ gallerySlides.length }}</span>
                                            <span class="gallery__number2">{{ slideIndex + 1 }}</span>
                                        </span>
                                        <div class="gallery__slides">
                                            <div class="gallery__slide" v-for="(slide, index) in gallerySlides" :key="index">
                                                <img class="gallery__img" v-show="index === slideIndex" :src="slide.img" @click="showModal(slide)" alt="" />
                                            </div>
                                        </div>
                                        <a @click.prevent="move(-1)" class="gallery__prev">&#10094;</a>
                                        <a @click.prevent="move(1)" class="gallery__next">&#10095;</a>
                                        <div class="gallery__content">
                                            <div class="gallery__items">
                                                <div class="gallery__item" v-for="(slide, index) in gallerySlides" :key="index" :class="{ 'gallery__item--is-acitve': index === slideIndex }">
                                                    <img :src="slide.img" @click="currentSlide(index)" class="gallery__item-img" />
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div class="product__left">
                                <div class="product__category">دسته وب</div>
                                <div class="product__info">
                                    <h1 class="product__title">محصول شماره یک یک شماره</h1>

                                    <div class="rating">
                                        <div class="rating__star">
                                            <span class="rating__rate" v-for="(rate, index) in ratings" :key="index" :data-width="`${rate.width}%`" :data-title="rate.title" @click="rateWidth = rate.width" @mouseover="handleMouseOver" @mouseleave="handleMouseLeave"> </span>
                                        </div>
                                        <div class="rating__fstar" :style="isMouseEnter ? 'width:0' : `width:${rateWidth}%`">
                                            <span class="rating__frate" v-for="(item, index) in 5" :key="index"></span>
                                        </div>
                                    </div>
                                    <div class="rating-star">
                                        <span class="rating__num">({{ rateWidth }})</span>
                                    </div>
                                </div>
                                <div class="controls">
                                    <Multiselete :options="colorsOption" v-model="seletedColor" placeholder="رنگ مورد نظر را انتخاب کنید" />
                                    <!-- <Multiselete :options="langsOption" v-model="seletedLangs" :multiple="true" :close-on-select="false" :clear-on-select="false" label="name" placeholder="زبان خود را انتخاب کنید" /> -->
                                    <Multiselete v-model="seletedLangs" :options="langsOption" :multiple="true" :close-on-select="false" :clear-on-select="false" :preserve-search="true" placeholder="Pick some" label="name" track-by="name" :preselect-first="true">
                                        <template slot="selection" slot-scope="{ values, search, isOpen }"
                                            ><span class="multiselect__single" v-if="values.length &amp;&amp; !isOpen">{{ seletedLangs.length }} options selected</span></template
                                        >
                                    </Multiselete>
                                </div>
                                <div class="product__price">
                                    <h5 class="product__campare-price">12,200,200</h5>
                                    <h5 class="product__total-price">22,200,200 تومان</h5>
                                    <div class="product__discount">
                                        <span class="product__dicount-value">6%</span>
                                        <span class="product__dicount-label">تخفیف</span>
                                    </div>
                                </div>
                                <div class="alert alert--warning d--none">این محصول موجود نمی باشد</div>
                                <div class="product__buttons">
                                    <button class="btn btn--brand btn--boxshadow btn--add-basket">افزدون به سبد خرید</button>
                                    <button class="btn btn-transparent btn--fav" :class="{'btn-fav--is-active':isFavorate}" @click="isFavorate = !isFavorate" >افردون به علاقه مندی ها</button>
                                    <a href="" class="btn btn-transparent btn--compare">مقایسه</a>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <SwiprweSlider>
                    <template v-slot:title>
                        related products
                    </template>
                    <router-link :to="{ name: 'Product', params: { id: index + 1 } }" v-for="(item, index) in 6" :key="index" class="swiper-slide">
                        <div class="card">
                            <div class="card__image"><img :src="require(`../assets/img/slider/${index + 1}.jpg`)" alt="" class="card__img" /></div>
                            <div class="card__title2">
                                گوشی موبایل سامسونگ مدل نوت 20
                            </div>
                            <div class="card__price">
                                <span class="card__compare-price">12,200,000</span>
                                <span class="card__total-price">12,400,000 تومان</span>
                            </div>
                            <span class="card__discount">%6</span>
                        </div>
                    </router-link>
                </SwiprweSlider>
                <ProductTab />
            </div>
        </main>
    </div>
</template>
<script>
import moment from 'moment';
import '../assets/css/modal.css';
import 'vue-multiselect/dist/vue-multiselect.min.css';
import Multiselete from 'vue-multiselect';
import SwiprweSlider from '../components/SwiperSlider.vue';
import ProductTab from '../components/ProductTab.vue';

export default {
    metaInfo: {
        title: 'محصول',
    },
    components: {
        Multiselete,
        SwiprweSlider,
        ProductTab,
    },
    data() {
        return {
            dateCountDown: moment('2021-09-07 00:00:00'),
            dif: null,
            countDownInterval: null,
            gallerySlides: [{ img: require('../assets/img/slider/1.jpg') }, { img: require('../assets/img/slider/2.jpg') }, { img: require('../assets/img/slider/3.jpg') }, { img: require('../assets/img/slider/4.jpg') }, { img: require('../assets/img/slider/5.jpg') }, { img: require('../assets/img/slider/6.jpg') }],
            slideIndex: 0,
            isShowingModal: false,
            modalImg: null,
            ratings: [
                { width: 100, title: 'عالی' },
                { width: 80, title: 'خوب' },
                { width: 60, title: 'معمولی' },
                { width: 40, title: 'ضعیف' },
                { width: 20, title: 'بد' },
            ],
            rateWidth: 0,
            isMouseEnter: false,
            seletedColor: '',
            colorsOption: ['قرمز', 'سبز', 'آبی', 'سفید'],
            seletedLangs: [],
            langsOption: [
                { name: 'Vue.js', language: 'JavaScript' },
                { name: 'Adonis', language: 'JavaScript' },
                { name: 'Rails', language: 'Ruby' },
                { name: 'Sinatra', language: 'Ruby' },
                { name: 'Laravel', language: 'PHP' },
                { name: 'Phoenix', language: 'Elixir' },
            ],
            isFavorate:false,
        };
    },
    methods: {
        move(n) {
            if (this.gallerySlides.length <= this.slideIndex + n) {
                this.slideIndex = 0;
            } else if (this.slideIndex + n < 0) {
                this.slideIndex = this.gallerySlides.length - 1;
            } else {
                this.slideIndex += n;
            }
        },
        currentSlide(n) {
            this.slideIndex = n;
        },
        showModal(slide) {
            this.modalImg = slide;
            this.isShowingModal = true;
            document.body.style.overflow = 'hidden';
        },
        closeModal() {
            this.isShowingModal = false;
            document.body.style.overflow = 'unset';
        },
        handleMouseOver() {
            // console.log('overd');
            this.isMouseEnter = true;
        },
        handleMouseLeave() {
            // console.log('overd');
            this.isMouseEnter = false;
        },
    },
    created() {
        this.countDownInterval = setInterval(() => {
            let diffTime = this.dateCountDown.diff(moment());
            let duration = moment.duration(diffTime);
            this.dif = `${Math.floor(duration.asDays())} روز :${duration.hours()} ساعت :${duration.minutes()} دقیقه :${duration.seconds()} ثانیه `;
        }, 1000);

        window.scrollTo({
            top: 0,
            behavior: 'smooth',
        });
    },
    destroyed() {
        clearInterval(this.countDownInterval);
    },
};
</script>
<style lang=""></style>
